<template>
    <div>
        <!-- Mensagem de Erro -->
        <v-alert v-if="msg_enviar_email" type="error">
            {{ msg_enviar_email }}
        </v-alert>
        <!-- /Mensagem de Erro -->

        <span>
            <v-icon>mdi-information</v-icon>
            Insira seu e-mail cadastrado
        </span>

        <v-form ref="form_email" class="mt-5">
            <v-text-field
              label="E-mail"
              v-model.trim="form.email"
              :rules="[rules.obrigatorio, rules.email]"
              prepend-icon="mdi-email"
              type="email"
              color="primary"
              clear-icon="mdi-close-circle"
              clearable
            ></v-text-field>
        </v-form>

        <div class="text-center mt-3 mb-3">
            <v-btn 
              class="rounded-lg mr-2"
              color="botao_2"
              text
              @click="cancelar()"
            >Cancelar</v-btn>

            <v-btn
              @click="enviar_email(form)"
              class="rounded-lg"
              color="botao_1"
              dark
            >Proximo</v-btn>
        </div>
    </div>
</template>

<script>
import api from "@/services/api";
import { mapMutations } from "vuex";

export default {
    name: "EmailCadastrado",

    data: () => ({
        msg_enviar_email: "",
        form: {},
        rules: {
            obrigatorio: (v) => !!v || "Este campo é obrigatório",
            email: v => /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'E-mail inválido'
        },
    }),

    methods: {
        ...mapMutations(["CarregandoMutation"]),

        enviar_email(form) {
            if (this.$refs.form_email.validate()) {
                this.CarregandoMutation();

                api.post('perfil/enviar-codigo/', form)
                .then((response) => {
                    if (response.data.status == 'ok') {
                        this.$emit("form-submit", form.email);
                        this.msg_enviar_email = '';
                    } else {
                        this.msg_enviar_email = response.data.msg;
                    }
                })
                .catch((error) => {
                    console.log(error);
                })
                .finally(() => {
                    this.CarregandoMutation();
                });
            }
        },

        cancelar() {
            this.$emit("cancelar");
        }
    },
}
</script>
